@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;700;800&display=swap");
body {
  font-family: "Inter", sans-serif;
}

h1 {
  font-weight: 800;
  font-size: 2.5rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

p {
  line-height: 1.5;
}

.description {
  padding-top: 0.75rem;
}

a {
  color: black;
  font-weight: bolder;
  text-decoration: none;
}

.light {
  color: #d2d2d2;
}

.mini-subtitle {
  text-transform: uppercase;
  font-size: 0.75rem;
  font-weight: 600;
  color: #D2D2D2;
  margin: 1rem 0;
}

@media screen and (min-width: 1000px) {
  h1 {
    font-weight: 800;
    font-size: 4rem;
  }
}